.container {
  height: 100%;
  width: 100%;
  max-width: 1800px;
  margin: 0 auto; }

.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
  position: absolute !important;
  white-space: nowrap;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.responsive-img-wrapper {
  display: inline-block;
  vertical-align: middle; }
  .responsive-img-wrapper img {
    display: block;
    height: auto;
    max-width: 100%; }

.hidden,
[hidden] {
  display: none !important; }

/* apply a natural box layout model to all elements */
*,
*:before,
*:after {
  box-sizing: border-box; }

* {
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

::selection {
  background: #c05527;
  color: #ffffff; }

::-moz-selection {
  background: #c05527;
  color: #ffffff; }

:focus {
  outline: 1px solid #c05527; }

html {
  color: #060c5c;
  font-size: 16px;
  transition: 0.2s ease-out font, 0.2s ease-out color; }

ul li,
ol li {
  list-style-type: none;
  /* remove bullets */ }
  ul li::before,
  ol li::before {
    content: "\200B";
    /* add zero-width space */ }

body {
  color: #000000;
  font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif;
  font-size: 16px;
  line-height: 1.5625;
  transition: 0.2s font, 0.2s color; }

h1,
.h1 {
  color: #000000;
  font-family: "abril-fatface", "Rockwell", Georgia, serif;
  font-size: 2.5rem;
  font-style: italic;
  font-weight: normal; }

h2,
.h2 {
  color: #000000;
  font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif;
  font-size: 2.1875rem;
  text-transform: uppercase; }

h3,
.h3 {
  color: #000000;
  font-family: "abril-fatface", "Rockwell", Georgia, serif;
  font-size: 1.875rem; }

h4,
.h4 {
  color: #000000;
  font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif;
  font-size: 2rem; }

h5,
.h5 {
  color: #000000;
  font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif;
  font-size: 1.25rem; }

h6,
.h6 {
  color: #000000;
  font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif; }

strong {
  font-weight: 700; }

em {
  font-style: italic; }

a {
  color: #060c5c;
  text-decoration: none; }
  a:focus {
    text-decoration: underline; }
  a:hover, a:active {
    color: #c05527;
    text-decoration: underline; }

.hide-robot {
  display: none; }

fieldset {
  border: none;
  padding: 0; }

form {
  margin: -12.5px auto; }
  form label {
    display: block;
    color: #ffffff;
    font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif;
    font-size: 2.1875rem;
    line-height: 1.08;
    margin: 12.5px auto;
    max-width: 600px; }
    form label span {
      display: block; }

input[type="text"],
input[type="email"] {
  font-size: 1rem;
  line-height: 2;
  padding: 0 0.5em;
  width: 100%; }
  input[type="text"]:focus,
  input[type="email"]:focus {
    outline: 1px solid #c05527;
    outline-offset: 4px; }

[type="submit"] {
  display: block;
  cursor: pointer;
  border: 1px solid #ffffff;
  border-radius: 6px;
  background: transparent;
  color: #ffffff;
  font-size: 1.5rem;
  text-transform: uppercase;
  padding: 10px 20px;
  margin: 12.5px auto;
  width: 100%;
  max-width: 600px;
  transition: 0.2s all ease-out; }
  @media screen and (min-width: 1024px) {
    [type="submit"] {
      font-size: 2.1875rem; } }
  [type="submit"]:hover, [type="submit"]:focus {
    color: #060c5c;
    background: #ffffff; }
  [type="submit"]:focus {
    outline: 1px solid #c05527;
    outline-offset: 4px; }

textarea {
  font-size: 1rem;
  line-height: 2;
  resize: none;
  width: 100%; }
  textarea:focus {
    outline: 1px solid #c05527;
    outline-offset: 4px; }

article a {
  color: #ffffff;
  font-size: 2.1875rem; }
  article a:hover, article a:active, article a:focus {
    color: #ededed;
    text-decoration: underline; }

.landing-hero {
  position: relative;
  background-color: #060c5c;
  color: #ffffff;
  padding: 20px 0 0 0;
  height: calc(100vh - 200px);
  text-align: center; }
  .landing-hero h1 {
    color: inherit;
    font-size: 5.125rem;
    line-height: 1.1; }
  .landing-hero .monogram {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 120px;
    margin: 0 auto; }
  .landing-hero-text {
    width: 100%; }

header.site-header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1; }
  @media screen and (min-width: 1024px) {
    header.site-header {
      position: fixed; } }

.page-template-homepage header.site-header {
  background: #060c5c; }

nav.main-navigation {
  display: block;
  width: 100%; }
  nav.main-navigation ul li {
    display: inline-block; }
  nav.main-navigation a {
    display: inline-block; }
    @media screen and (min-width: 1024px) {
      nav.main-navigation a {
        font-size: 2.1875rem;
        line-height: 1.08; } }

@media screen and (min-width: 1024px) {
  .page-template-homepage nav.main-navigation {
    padding: 0 0 0 70px; } }

.page-template-homepage nav.main-navigation ul li {
  margin-right: 15px; }
  .page-template-homepage nav.main-navigation ul li:last-of-type {
    margin-right: 0; }
  @media screen and (min-width: 1024px) {
    .page-template-homepage nav.main-navigation ul li {
      margin-right: 30px; } }

.page-template-homepage nav.main-navigation a {
  color: #ffffff;
  font-size: 2.1875rem; }
  .page-template-homepage nav.main-navigation a:hover, .page-template-homepage nav.main-navigation a:active, .page-template-homepage nav.main-navigation a:focus {
    color: #ededed;
    text-decoration: underline; }

.site-footer {
  color: #ffffff;
  background: #060c5c; }
  .site-footer h3 {
    color: #ffffff;
    font-family: "abril-fatface", "Rockwell", Georgia, serif;
    line-height: 1.1;
    font-weight: normal;
    margin-bottom: 20px;
    text-align: center; }
    @media screen and (min-width: 1024px) {
      .site-footer h3 {
        text-align: left; } }
  .site-footer h6 {
    color: #ffffff;
    font-family: "mr-eaves-modern", "Century Gothic", Verdana, sans-serif;
    font-size: 2.1875rem;
    line-height: 1.08;
    font-weight: normal;
    margin-bottom: 10px; }
  .site-footer a {
    color: #ffffff;
    font-size: 1.5rem;
    line-height: 1; }
    .site-footer a:hover, .site-footer a:focus, .site-footer a:active {
      color: #ffffff;
      text-decoration: underline; }
    @media screen and (min-width: 1024px) {
      .site-footer a {
        font-size: 2.1875rem;
        line-height: 1.08; } }
  .site-footer ul {
    padding: 0; }
  .site-footer div.contact-form {
    margin: 0 auto;
    width: 100%;
    max-width: calc(100% - 20px); }
    @media screen and (min-width: 1024px) {
      .site-footer div.contact-form {
        max-width: 425px; } }
  .site-footer .social-media .wrap {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: baseline; }
  .site-footer .footer-split {
    margin: 25px auto;
    width: 100%;
    max-width: calc(100% - 20px);
    padding: 0; }
    @media screen and (min-width: 1024px) {
      .site-footer .footer-split {
        margin: 0 auto;
        flex: 0 1 50%;
        padding: 0 25px;
        width: 50%; } }
  .site-footer .footer-top {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: baseline;
    padding: 0 25px; }
    @media screen and (min-width: 1024px) {
      .site-footer .footer-top {
        padding: 50px 12.5px; }
        .site-footer .footer-top::after {
          position: absolute;
          top: 50px;
          left: 50%;
          bottom: 50px;
          transform: translateX(-50%);
          display: block;
          background: #ffffff;
          width: 2px; } }
  .site-footer .footer-bottom {
    display: block;
    text-align: center;
    width: 100%; }
